* {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

body {
    background-color: rgb(34, 25, 25);
    color: snow;
    padding: 60px;
}

input {
    font-size: 1.2rem;
    width: 100%;
}

.card {
    align-content: center;
    border: 1px solid gray;
    border-bottom: 6px;
    border-radius: 6px;
    margin-right: 10px;
    overflow: hidden;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card img {
    max-height: 170px;
}

.card p {
    padding: 5px;
    text-align: center;
}